body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
/* Making Front Size Consistent*/
.MuiFormLabel-root{
  font-size: 1.5rem !important;
  line-height: .5 !important;
 
 }

.slick-arrow{
	position: absolute;
	width: 100px;
	height: 100px;
	top: 45%;
	cursor:pointer;
}
.userDescThemeFont{
    font-size:0.9em !important;
}
.userDescValueFont{
    font-size:0.8em !important;
}
.slick-prev {
  left: 40px !important;
  z-index: 1;
}
.slick-next {
  right: 60px !important;
  z-index: 1;
}
.slick-prev:before, .slick-next:before  {
  content: '' !important;
}

